import React, { Component } from 'react'
import facebook from '../images/facebook.svg'
import linkedin from '../images/linkedin.svg'
import mail from '../images/mail.svg'

class Footer extends Component {
	constructor() {
		super();
		this.state = {
			intervalId: 0
		};
		this.scrollToTop = this.scrollToTop.bind(this);
	}

	scrollStep() {
		if (window.pageYOffset === 0) {
			clearInterval(this.state.intervalId);
		}
		window.scroll(0, window.pageYOffset - 50);
	}
  
	scrollToTop(e) {
		e.preventDefault();
		const intervalId = setInterval(this.scrollStep.bind(this), 10);
		this.setState({ intervalId });
	}

	render() {
		return (
			<div className="footer">
				<div className="back-to-top">
					<a href="#" onClick={this.scrollToTop}>Back To Top</a>
				</div>
				<div className="social-links">
					<a href="mailto:pd.fiksson@gmail.com">
						<img src={mail} alt="mail"/>
					</a>
					<a href="https://www.linkedin.com/in/polina-fiksson/">
						<img src={linkedin} alt="linkedin"/>
					</a>
					<a href="https://www.facebook.com/polina.fiksson">
						<img src={facebook} alt="facebook"/>
					</a>
				</div>
				<div className="copyright-note">
					<p>© 2018 Polina Fiksson. All rights reserved.</p>
				</div>
			</div>
		)
	}
}


export default Footer